.input-checkbox {
  appearance: none;
  user-select: none;
  background: rgba(0, 0, 0, 0);
  position: relative;
}
.input-checkbox::after {
  display: block;
  content: "";
  width: 0.2em;
  height: 0.5em;
  background-color: transparent;
  position: absolute;
  top: 40%;
  left: 50%;
  transform: translate(-50%, -50%) rotate(45deg);
  opacity: 0;
  transition: opacity 0.2s ease-in-out;
  border-style: solid;
  border-color: #faf6ec;
  border-color: var(--light, #faf6ec);
  border-bottom-width: 2px;
  border-right-width: 2px;
  border-top: none;
  border-left: none;
}
.input-checkbox::before {
  content: "";
  width: 1em;
  height: 1em;
  background-color: #a4a4a4;
  background-color: var(--gray-light, #a4a4a4);
  display: inline-block;
  border-radius: 0.1875em;
  vertical-align: middle;
  transition: background-color 0.2s ease-in-out;
  margin-top: -0.3em;
}
.input-checkbox:hover::before, .input-checkbox:focus::before {
  background-color: #8a8a8a;
  background-color: var(--gray);
}
.input-checkbox:checked::after {
  opacity: 1;
}
.input-checkbox:checked::before {
  background-color: hotpink;
  background-color: var(--accent-color, hotpink);
}
.input-radio {
  appearance: none;
  user-select: none;
  background: rgba(0, 0, 0, 0);
  position: relative;
}
.input-radio::after {
  display: block;
  content: "";
  background-color: hotpink;
  background-color: var(--accent-color, hotpink);
  position: absolute;
  height: 0.35em;
  width: 0.35em;
  top: 0.56em;
  left: 0.19em;
  opacity: 0;
  transition: opacity 0.2s ease-in-out;
  border-radius: 50%;
}
.input-radio::before {
  content: "";
  width: 0.7em;
  height: 0.7em;
  background-color: #faf6ec;
  background-color: var(--white);
  display: inline-block;
  border-radius: 50%;
  vertical-align: middle;
  transition: box-shadow 0.2s ease-in-out;
  box-shadow: 0 0 0 0.1em #8a8a8a;
  box-shadow: 0 0 0 0.1em var(--gray, #8a8a8a);
}
.input-radio:checked::after {
  opacity: 1;
}
.input-radio:checked::before {
  box-shadow: 0 0 0 0.1em hotpink;
  box-shadow: 0 0 0 0.1em var(--accent-color, hotpink);
}

